.App{
  padding: 0;
  margin:0;
  width: 100vw;
  height: 100vh;
  font-family: 'Quicksand', sans-serif;
}

.Title {
  padding: 1vw 2vw 2vw 2vw;
  text-align: center;
}

h1{
  font-size: 3em;
  margin-bottom: .5em;
}

.CardList {
  display: flex;
  width: 100vw;
  flex-direction: row;
  flex-wrap: wrap;
}

button {
  background: none;
  font-family: 'Quicksand', sans-serif;
  font-size: 1.5em;
  margin: 0 1vw;
  border:none;
  text-decoration: none;
}

button:hover {
    font-weight: 600;
}


button:focus {
  outline:none; 
  font-weight: 600;
}

.Filters {
  display: flex;
  justify-content: center;
}